html {
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  width: 100%;
  min-width: 320px;
  margin: 0;
  position: relative;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  word-wrap: normal;
  color: #011c40;
  background-color: #e5e5e5; }

a {
  text-decoration: none; }

img {
  -o-object-fit: contain;
     object-fit: contain;
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  min-width: initial;
  height: 1px;
  min-height: initial;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  min-width: 1024px;
  width: 100%;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .container {
      width: 768px; } }
  @media (max-width: 767px) {
    .container {
      width: 320px; } }
